(function () {
    "use strict";

    // 
    //  notynotify

    var notify = {
        _core: $.noop,
        warn: $.noop,
        success: $.noop,
        error: $.noop,
        confirm: $.noop
    };


    docmana.ui.notify = notify;

    if (window.noty == null) return;

    notify._core = function (option) {
        var n = noty($.extend({
            type: 'warning',
            dismissQueue: true,
            force: true,
            layout: 'topCenter',
            theme: 'relax',  // bootstrapTheme
            closeWith: ['click'],
            maxVisible: 1,
            animation: {
                open: { height: 'toggle' },
                close: { height: 'toggle' },
                easing: 'swing',
                speed: 100 // opening & closing animation speed
            },
            timeout: false,
            killer: true,
            modal: false
        }, option));

    };

    notify.warn = function (text) {
        notify._core({
            text: text,
            type: 'warning',
            timeout: 4000
        });
    };

    notify.success = function (text) {
        notify._core({
            text: text,
            timeout: 2000,
            type: 'success'
        });
    };

    notify.error = function (text) {
        notify._core({
            text: text,
            type: 'error',
            timeout: false
        });
    };

    notify.confirm = function (successCb, cancelCb) {
        notify._core({
            text: '',
            type: 'confirm',
            modal: true,
            buttons: [{
                addClass: 'btn btn-primary btn-xs', text: '', onClick: function ($noty) {
                    $noty.close();
                    successCb();
                }
            }, {
                addClass: 'btn btn-danger btn-xs', text: '', onClick: function ($noty) {
                    $noty.close();
                    cancelCb();
                }
            }]
        });
    };


})();

